<template>
  <div id="app">
    <div class="todo-container">
      <h1 class="hh1">ToDoList</h1>
      <div class="todo-wrap">
        <MyTop @addTodo="addTodo"></MyTop>
        <MyList :todos="todos"></MyList>
        <MyEnd
          :todos="todos"
          @checkAllTodo="checkAllTodo"
          @clearAllTodo="clearAllTodo"
        >
        </MyEnd>
      </div>
      <div id="pic">
        <img src="../public/naxida.png" alt="纳西妲" class="img1" />
      </div>
      <h3 class="hh3">2022.10.19 by zhy</h3>
    </div>
  </div>
</template>

<script>
import MyTop from "./components/MyTop.vue";
import MyList from "./components/MyList.vue";
import MyEnd from "./components/MyEnd.vue";
export default {
  name: "App",
  components: {
    MyEnd,
    MyTop,
    MyList,
  },

  data() {
    return {
      todos: JSON.parse(localStorage.getItem("todos")) || [],
    };
  },
  methods: {
    // 添加一个todo
    addTodo(todoObj) {
      this.todos.unshift(todoObj);
    },
    // 勾选或取消勾选一个todo
    checkTodo(id) {
      this.todos.forEach((todo) => {
        if (todo.id === id) todo.done = !todo.done;
      });
    },
    // 删除一个todo
    deleteTodo(id) {
      this.todos = this.todos.filter((todo) => {
        return todo.id !== id;
      });
    },
    // 全选或取消全选
    checkAllTodo(done) {
      this.todos.forEach((todo) => {
        todo.done = done;
      });
    },
    // 清除所有已经完成的todo
    clearAllTodo() {
      this.todos = this.todos.filter((todo) => {
        return !todo.done;
      });
    },
  },
  watch: {
    todos: {
      deep: true,
      handler(value) {
        localStorage.setItem("todos", JSON.stringify(value));
      },
    },
  },
  mounted() {
    this.$bus.$on("checkTodo", this.checkTodo);
    this.$bus.$on("deleteTodo", this.deleteTodo);
  },
  beforeDestroy() {
    this.$bus.$off("checkTodo");
    this.$bus.$off("deleteTodo");
  },
};
</script>


